body {
    display : flex;
    justify-content: center;
    align-items: center;
}
.header {
    padding : 10px;
}
.home {
    display : grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
}
.movieComponent {
    border : 1px solid red;
    width : 250px;
    height : 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin : 10px;
}
